
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import background from "./assets/fondo.png";
import logo from "./assets/logo.png";


function Head() {
  return (
    <Container fluid style={{ backgroundImage: `url(${background})`, backgroundPosition:'center top', backgroundSize: '100% 100%' , height: '100vh'}} >
        <Row class='justify-content-center' style={{paddingTop: '4rem'}}>
            <Col></Col>
            <Col>
                 <img src={logo} style={{width: '30vw'}} alt="Logo" />
            </Col>
            <Col></Col>
        </Row>        
        <Row style={{paddingTop: '2rem'}}>
            <h1 className='text-center' style={{color: 'white'}}>la policia no me cuida, me cuidan mis amigxs</h1> 
        </Row>
        <Row>
            <Col className='text-center'>
                <h5 style={{color: 'white', marginTop:'3rem'}}>Informacin</h5>
            </Col>
            <Col className='text-center'>
                 <h5 style={{color: 'white' , marginTop:'3rem'}}>Noticias</h5>
            </Col>
            <Col className='text-center'>
                 <h5 style={{color: 'white', marginTop:'3rem'}}>App</h5>
            </Col>
        </Row>
    </Container>
  );
}

export default Head;

